import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';

// Import translation files
import en from './locales/en.json';
import hi from './locales/hi.json';

// Language configuration
export const languages = [
    { code: 'en', name: 'English', native: 'English', script: 'Latin', dir: 'ltr' },
    { code: 'hi', name: 'Hindi', native: '', script: 'Devanagari', dir: 'ltr' },
    { code: 'gu', name: 'Gujarati', native: '', script: 'Gujarati', dir: 'ltr' },
    { code: 'mr', name: 'Marathi', native: '', script: 'Devanagari', dir: 'ltr' },
    { code: 'ta', name: 'Tamil', native: '', script: 'Tamil', dir: 'ltr' },
    { code: 'kn', name: 'Kannada', native: '', script: 'Kannada', dir: 'ltr' },
    { code: 'ml', name: 'Malayalam', native: '', script: 'Malayalam', dir: 'ltr' },
    { code: 'pa', name: 'Punjabi', native: '', script: 'Gurmukhi', dir: 'ltr' },
    { code: 'bn', name: 'Bengali', native: '', script: 'Bengali', dir: 'ltr' },
    { code: 'te', name: 'Telugu', native: '', script: 'Telugu', dir: 'ltr' },
    { code: 'or', name: 'Odia', native: '', script: 'Odia', dir: 'ltr' },
    { code: 'ur', name: 'Urdu', native: '', script: 'Arabic', dir: 'rtl' },
] as const;

export type LanguageCode = typeof languages[number]['code'];

i18n
    .use(LanguageDetector)
    .use(initReactI18next)
    .init({
        resources: {
            en: { translation: en },
            hi: { translation: hi },
            // Other languages will fall back to English
        },
        fallbackLng: 'en',
        debug: process.env.NODE_ENV === 'development',
        interpolation: {
            escapeValue: false, // React already escapes
        },
        detection: {
            order: ['localStorage', 'navigator'],
            caches: ['localStorage'],
        },
    });

export default i18n;
